          p.mb-0 Cuando se habla de la manipulacin de productos, se hace referencia a maniobrar las mercancas, hacerlas ms ligeras para una mejor manipulacin y posterior desplazamiento hacia cajas, estibas o consolidacin de la mercanca en el centro de distribucin; esta actividad se podr realizar de forma manual, automtica o semiautomtica.  La manipulacin tradicional puede ser de forma #[strong manual] o semiautomtica, mientras la manipulacin #[strong continua] es automatizada en su totalidad; en ambos casos lo que se pretende es realizar actividades de recepcin, almacenamiento, preparacin de pedidos y despacho.  

        p.mb-0 En aquellos sistemas manuales y de acuerdo a estndares para manejo de cargas en salud ocupacional, un operario de gnero masculino est en capacidad de trasladar cargas hasta de 25 kg con un recorrido mximo de 30 m, mientras que la mujer podr realizar traslados mximos de 15 kg; un mecanismo de apoyo para superar estas limitaciones y aumentar la capacidad tanto de carga como traslados son los diferentes equipos de manipulacin y movimentacin de cargas, importante tener en cuenta aspectos como rotacin de productos y espacio disponible al momento de elegir.  A continuacin, se describen las caractersticas de las principales formas de manipulacin de los productos:

