        p En la gestin del almacn se requieren establecer aquellas tcnicas de manipulacin ms favorables de acuerdo a las caractersticas del producto, los medios o equipos de manipulacin necesarios para mover y trasladar mercancas dentro del almacn y los medios o equipos de almacenamiento, que hace referencia a los estantes, cajas, armarios, suelos, silos, contenedores, etc. que sirven para guardar mercancas requeridas posteriormente.

        p Existe un gran nmero de equipos de almacenamiento, as como los diversos tipos de mercancas, lo que se requiere es analizar y seleccionar los ms adecuados para los productos.  Incluso se puede solicitar asesora con el proveedor sobre nuevas tecnologas, materiales o formas de almacenar.  De igual forma las actividades de manipulacin de mercancas se pueden utilizar sistemas manuales o automatizados, lo cual requiere recursos humanos y materiales. 

