            p.mb-0 #[strong Estticos sin traslado:] se utilizan para almacenar mercanca a granel, tienen un orificio de entrada y uno de salida.  Son muy utilizados para manejar semillas y minerales, pueden ser cilndricos o poligonales y los ms comunes son los silos de almacenamiento.

    p.mb-5 Teniendo claridad sobre los diferentes equipos para la manipulacin de las mercancas, se puede dar inicio a manipular los productos o pedidos, con el objetivo de reducir recorridos, disminuir tiempos, operaciones, y mantener una rotacin de <i>stock</i> precisa; para lograr eficiencia en este proceso es importante el desarrollo de las siguientes tareas:

