    p.mb-4 Para hablar de zonas de cargue y descargue, debemos recordar que el proceso logstico requiere de un conjunto de medios y mtodos para desarrollar la actividad o servicio especfico; por tanto, requiere de la optimizacin de los recursos para conseguir los mejores resultados. Uno de los principales recursos es el almacn, que sirve de elemento regulador en el flujo de las mercancas en la red logstica. El almacn podra definirse como aquella locacin donde se realizan funciones de recepcin, manipulacin, conservacin, proteccin y posterior despacho de productos.

    .tarjeta.color-primario.p-3.mb-5(data-aos="zoom-in")
      .row.justify-content-around.align-items-center
        .col-3.col-sm-2.col-lg-1
