            .col-sm-auto
              a.boton.color-acento-botones.texto-blanco(:href="obtenerLink('/downloads/4_Norma-Tecnica-Prevencin-1076_Seguridad-Muelles.pdf')" target="_blank")
                span Descargar
                i.fas.fa-file-download

    h3 Principales zonas del almacn

    .crd.crd--gradient01.mb-5
      .row.justify-content-center.align-items-center
        .col-auto.mb-5.mb-lg-0
          figure(data-aos="fade-right")
            img(src="@/assets/curso/tema1/t1_18.png", alt="alt").img300.m-auto
        .col-12.col-lg        
          p.mb-0 El espacio fsico donde se regula el flujo de entrada y salida de productos, resulta igual de indispensable en la cadena logstica. Sin embargo, se debe tener claro desde el diseo y la distribucin, cules son las zonas del almacn, como se describen a continuacin:

    .row.justify-content-center.mb-5
      .col-lg-10
        .crd.crd--slyder.p-5
          SlyderA(tipo="b")
            .row.justify-content-center.align-items-center
              .col-md-8.mb-4.mb-md-0
                h4.mb-4 <i>Layout</i> o diseo reas de bodega
                p Etapa previa al diseo, la organizacin distribuye el espacio sobre planos e identifica las diferentes zonas de la bodega; deben tenerse en cuenta algunos factores como: volumen de la operacin, tipo de mercanca y control de unidades.        
              .col-md-4
                figure
                  img(src='@/assets/curso/tema1/t1_19.png', alt='').img350.m-auto
                  
            .row.justify-content-center.align-items-center
              .col-md-8.mb-4.mb-md-0
                h4.mb-4 Zona de carga y descarga
                p Es el rea donde se desarrollan actividades de descarga de los vehculos que traen la mercanca procedente de los proveedores y las devoluciones que realizan los clientes; esta zona cuenta con muelles de carga y descarga.  Aqu se realiza el primer punto de control.      
              .col-md-4
                figure
                  img(src='@/assets/curso/tema1/t1_20.png', alt='').img350.m-auto

            .row.justify-content-center.align-items-center
              .col-md-8.mb-4.mb-md-0
                h4.mb-4 Zona de recepcin
                p Entrada de la mercanca, donde se realiza un control cuantitativo (unidades correctas con el registro) y cualitativo de la mercanca (calidad y estado).  Posterior ser clasificada y distribuida en la bodega.      
              .col-md-4
                figure
                  img(src='@/assets/curso/tema1/t1_21.png', alt='').img350.m-auto

            .row.justify-content-center.align-items-center
              .col-md-8.mb-4.mb-md-0
                h4.mb-4 Zona de envasado
                p Esta zona se encuentra en aquellas bodegas o almacenes que es necesario volver a envasar o repaletizar (estibar) y conformar nuevas unidades de carga, por razones de almacenamiento, sanidad e higiene.      
              .col-md-4
                figure
                  img(src='@/assets/curso/tema1/t1_22.png', alt='').img350.m-auto

            .row.justify-content-center.align-items-center
              .col-md-8.mb-4.mb-md-0
                h4.mb-4 Zona de cuarentena
                p Estas zonas aplican ms para aquellas bodegas que almacenan productos farmacuticos o agroalimentos, los cuales por normativa exige unos anlisis previos al almacenamiento.      
              .col-md-4
                figure
                  img(src='@/assets/curso/tema1/t1_23.png', alt='').img350.m-auto

            .row.justify-content-center.align-items-center
              .col-md-8.mb-4.mb-md-0
                h4.mb-4 Zona de almacenamiento
                p Son las zonas ms complejas de la bodega; es el lugar donde se depositan las mercancas hasta registrar la expedicin; en esta zona se diferencian dos reas: A) <i>stock</i>, rea destinada a la reserva de los productos. B) <i>Picking</i>, rea donde se preparan los pedidos para el embalaje.      
              .col-md-4
                figure
                  img(src='@/assets/curso/tema1/t1_24.png', alt='').img350.m-auto

            .row.justify-content-center.align-items-center
              .col-md-8.mb-4.mb-md-0
                h4.mb-4 Zona de consolidacin
                p La funcin principal de esta zona es agrupar variedad de productos que correspondan a un mismo pedido, incluso de diferentes proveedores y as realizar un envo de mayor volumen a menor costo.       
              .col-md-4
                figure
                  img(src='@/assets/curso/tema1/t1_25.png', alt='').img350.m-auto

            .row.justify-content-center.align-items-center
              .col-md-8.mb-4.mb-md-0
                h4.mb-4 Zona de embalaje
                p En esta zona se realiza todo el proceso necesario de acondicionamiento de los productos para protegerlos de manera temporal para posterior manipulacin y transporte; el embalaje puede ser manual o a travs de equipos automatizados.      
              .col-md-4
                figure
                  img(src='@/assets/curso/tema1/t1_26.png', alt='').img350.m-auto

            .row.justify-content-center.align-items-center
              .col-md-8.mb-4.mb-md-0
                h4.mb-4 Zona de control o despacho
                p La funcin de esta zona es verificar que las mercancas relacionadas en el pedido correspondan con los registros documentales de preparacin del pedido o solicitud del cliente.  Por lo regular en esta rea se acumulan las mercancas que sern cargadas en los vehculos.   
              .col-md-4
                figure
                  img(src='@/assets/curso/tema1/t1_27.png', alt='').img350.m-auto

            .row.justify-content-center.align-items-center
              .col-md-8.mb-4.mb-md-0
                h4.mb-4 Zona de espera
                p Esta zona es utilizada por lo general para atender los imprevistos que se puedan presentar como: rapidez o lentitud en la preparacin de los pedidos y que lleguen a generar adelantos o retrasos al momento de hacer entrega al transportador. 
              .col-md-4
                figure
                  img(src='@/assets/curso/tema1/t1_28.png', alt='').img350.m-auto           

            .row.justify-content-center.align-items-center
              .col-md-8.mb-4.mb-md-0
                h4.mb-4 Zona tcnica
                p Aqu se desarrollan actividades principalmente de mantenimiento de los equipos tcnicos y mecnicos que utilice el rea operativa.
              .col-md-4
                figure
                  img(src='@/assets/curso/tema1/t1_29.png', alt='').img350.m-auto

            .row.justify-content-center.align-items-center
              .col-md-8.mb-4.mb-md-0
                h4.mb-4 Zona administrativa
                p En otras palabras, es el rea destinada para las oficinas, atencin al cliente o los transportadores, salas de reunin, casino, vestieres; o como se denomina en calidad gestin organizacional del almacn.
              .col-md-4
                figure
                  img(src='@/assets/curso/tema1/t1_30.png', alt='').img350.m-auto
                  

    h3.mb-5 Cargue y descargue de mercancas

    .crd.crd--gradient01.mb-5
