    p.mb-5 Por lo general, los muelles de carga cuentan con varios elementos, los cuales tienen la funcionalidad de proteger la mercanca de daos externos y que no se pierda mucho tiempo en la operacin de carga y descarga; los elementos que hacen parte de ella, son: 

