            p Durante el desarrollo de las operaciones de la cadena logstica, se pueden presentar actividades que representen un mayor nivel de ocurrencia y potencialidad de generar algn dao; estos son la carga y descarga de mercancas, por lo cual requiere una descripcin de las actividades para identificar el proceso y prevenir incidentes.
            p.mb-0 Para ampliar la informacin sobre este tema, lo invitamos a ver los vdeos Distribucin del almacn y Carga y descarga de mercancas, los cuales se encuentran en el material complementario.

