    #t_1_3.titulo-segundo.color-acento-contenido
      h2 1.3 Planeacin de rutas   

    .row.justify-content-center.mb-5
      .col-lg-11
        .bloque-texto-g.clr--pink.p-3.p-sm-4.p-md-5
          .bloque-texto-g__img(
            :style="{'background-image': `url(${require('@/assets/curso/tema1/t1_39.png')})`}"
          )
          .bloque-texto-g__texto.p-5
            p.mb-0 En los ltimos tiempos se ha incrementado el comercio electrnico, por tanto, los pedidos a domicilio; lo que significa una reactivacin de la economa de manera positiva, mientras para el sector logstica podra verlo como una oportunidad para fortalecer y buscar soluciones ptimas para el transporte logstico en los centros de distribucin.  El cual debe ser ms eficiente; pues de la programacin adecuada de las rutas de reparto depende la optimizacin en los tiempos de entrega, reduccin de los costos y satisfaccin de los clientes.

    .tarjeta.color-primario.p-3.mb-5(data-aos="zoom-in")
      .row.justify-content-around.align-items-center
        .col-3.col-sm-2.col-lg-1
          img(src="@/assets/curso/tema1/t1_17.png")
        .col
          .row.justify-content-between.align-items-center
            .col.mb-3.mb-sm-0              
              p.mb-0 Para ampliar la informacin, lo invitamos a descargar el documento #[strong 8 pasos para planificar y optimizar rutas de transporte].
            .col-sm-auto
              a.boton.color-acento-botones.texto-blanco(:href="obtenerLink('/downloads/2_Guia-planificacion-optimizacion-rutas_Bee.pdf')" target="_blank")
                span Descargar
                i.fas.fa-file-download 

    
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-5.mb-5.mb-lg-0
        figure(data-aos="fade-right")
          img(src="@/assets/curso/tema1/t1_40.png", alt="alt").img500.m-auto
      .col-lg-7
        .sq--pink 
          p La planeacin de rutas consiste en definir sobre un plano o un mapa los recorridos que se deben realizar para la distribucin, teniendo en cuenta previamente los puntos de entrega, estado y nivel de congestin de las vas, medios de transporte necesarios y disponibles para realizar los recorridos.  En consecuencia, para lograr un eficiente servicio en los tiempos de entrega, aumentar el nmero de encomiendas y reducir el consumo de combustible, se requiere establecer una planeacin de rutas para entrega en el menor tiempo posible y costos razonables.  As la adecuada planeacin de rutas permite realizar actividades adicionales que agreguen valor a la operacin; como recogidas extra, entregas adicionales o recibir devoluciones que sumen puntos en la logstica inversa, e ir preparando la mercanca para la fase de ltima milla (entre menos millas se conducen, menos cantidad de vehculos y menor consumo de combustible sern necesarios).

    p Las actividades a desarrollar para una ptima planificacin de rutas son:

    .row.justify-content-center
      .col-lg-10

        PasosA.color-primario.mb-5(tipo="n")
          div
            h3 Recursos disponibles
            p Es determinar con qu se cuenta (recursos tcnicos: unidades de transporte-unidades de combustible y recurso humano: cantidad de conductores), de acuerdo a las caractersticas de los productos para hacer el despacho de los pedidos y lograr entregas de forma eficaz y eficiente.
          div
            h3 Puntos de entrega
            p Identificar los puntos de entrega (direcciones o coordenadas) en el mapa disponible, de acuerdo al sistema informtico o <i>software</i> y base de datos de los clientes.
          div
            h3 Priorizacin
            p Establecer factores externos que influyen en las rutas y entregas como: capacidad de los vehculos para transportar y caractersticas de la mercanca.
          div
            h3 Variables de la zona 
            p Distancias entre el almacn y puntos de entrega, zonas en comn para varios pedidos, identificacin de vas principales y alternas, rendimiento del combustible, paradas necesarias y condiciones de las vas.
          div
            h3 Diseo de rutas
            p Esta actividad es la unin de todas las anteriores, es necesario todos los anteriores insumos, recursos, ubicacin, variables, objetivos para establecer las rutas en el mapa disponible e integrar los elementos mencionados.
          div
            h3 Unidad de transporte 
            p Asignacin de los medios de transporte, dependiendo de la disponibilidad tanto del equipo como del conductor, volumen y peso de la carga, cantidad de entregas y de rutas, valor econmico mximo por vehculo. 
          div
            h3 Rendimiento de las entregas
            p El objetivo es calcular el rendimiento de las entregas e identificar los aspectos por mejorar.
          div
            h3 Optimizar las rutas
            p En este paso se cierra el ciclo, se debe actuar e implementar las acciones necesarias para optimizar la planeacin de las rutas; en cuanto a distancias, costo por kilmetro, costos fijos y costos totales de la operacin.

    h3.mb-4 Elementos clave en la planificacin y optimizacin de rutas

    p Los elementos que se necesitan, para lograr una buena planificacin y xito de aquellas compaas que se dedican especficamente al transporte y entrega de mercancas, deben contar con las mejores herramientas de trabajo, con optimizadores de rutas de transporte para medir el desempeo de los empleados en tiempo real. 

    p.mb-5 Una buena planificacin de rutas empieza a ser identificada por cada una de las acciones, destinos, rutas y cargas comunes, que nos permita comprender dnde se debe llegar y cundo crear rutas eficientes. Algunos elementos claves en la planificacin y optimizacin de rutas son:

    figure.mb-5
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/z-rEIdSFkqo" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)       

