    #t_1_4.titulo-segundo.color-acento-contenido
      h2 1.4 Cadena logstica
      
    p.mb-5 Para comprender el concepto de cadena logstica, debemos empezar por interpretar el trmino logstica, el cual hace referencia a planificar y colocar en marcha las actividades necesarias para desarrollar un proyecto; a nivel de empresa la logstica trata de la forma de organizar el aprovisionamiento de materiales, produccin, almacn y distribucin de productos que adoptan las empresas. Por ejemplo, cuando asistimos a un supermercado, detrs de los productos que compramos para el consumo existe un sistema logstico que coordina las zonas de cargue y descargue, el lugar de almacenamiento de los productos, el lugar de empaque para productos de promocin, el sistema de transporte para llevarlos a las estanteras y el punto para hacer la entrega (cajas registradoras).

    .row.justify-content-center.mb-5
      .col-lg-4.mb-5.mb-lg-0
        figure(data-aos="fade-right")
          img(src="@/assets/curso/tema1/t1_41.png", alt="alt").img400.m-auto
      .col-lg-8
        .sq--pink
          p Existen dos trminos que son similares, pero no iguales: cadena logstica y cadena de suministro, y se prestan para confusin en algunas ocasiones. La cadena logstica se fundamenta en aquellos medios y mtodos necesarios para organizar las mercancas, llevarlas en los trminos y condiciones pactadas con el cliente final.  Mientras que la cadena de suministro se refiere a todas las tareas relacionadas con la fabricacin de productos, desde la adquisicin de materias primas, transformacin de bienes y almacenamiento (dentro del ciclo de producto) hasta hacer la entrega al cliente final.  Por tanto, la cadena logstica hace parte de la cadena de suministro. Conozcamos las caractersticas ms relevantes:

    TabsC.color-acento-contenido.mb-5
      .py-3.py-md-4(titulo="<i>Lead time</i>")
        .row
          .col-md-7.mb-4.mb-md-0
            h4 <i>Lead time</i>
            p Mientras en la cadena de suministro se habla de todo el proceso de aprovisionamiento, en la cadena logstica juega un papel importante el tiempo que lleva en realizar la operacin, desde el momento que llega la orden de pedido, preparacin de la mercanca y entrega al cliente.
        
          .col-md-3
            figure(data-aos="fade-left")
              img(src='@/assets/curso/tema1/t1_42.png').img300.m-auto

      .py-3.py-md-4(titulo="Cadena de valor aadido")
        .row
          .col-md-7.mb-4.mb-md-0
            h4 Cadena de valor aadido
            p El trmino cadena de valor aadido hace referencia a la diferencia entre los beneficios recibidos por el cliente y los costos que ha demandado adquirir el producto; sin embargo, al interior de las organizaciones muchas de ellas logran desarrollar actividades que le agreguen valor al cliente final, las cuales se dividen en dos grandes grupos, as:
        
          .col-md-4
            figure(data-aos="fade-left")
              img(src='@/assets/curso/tema1/t1_43.svg').img450.m-auto

    p.mb-5 Y la diferencia entre cadena logstica y cadena de suministro es:

    .titulo-sexto.color-acento-contenido
      h5 Tabla 5.
      span Diferencia entre cadena logstica y cadena de suministro

