    #t_1_5.titulo-segundo.color-acento-contenido
      h2 1.5 Informacin documentada

    p Los sistemas de informacin gestionan aquellas actividades desarrolladas dentro del almacn, cuya finalidad es apoyar la gestin fsica de la mercanca, desde que ingresa al almacn, hasta que sale en los centros de almacenamiento o distribucin, e incluso hacer seguimiento a las devoluciones e inconsistencias; algunos de ellos son:

    TabsA.color-primario.mb-5
      .crd.crd--yellow(titulo="SGA")
        h4.px-4.pt-4 SGA (Sistema de Gestin de Almacn)
        p.mb-4.px-4 Cuya funcin ms importante es mantener controlado el <i>stock</i> de mercanca (nmero de existencias registradas, equivalente a existencias fsicas).
        figure
          img(src="@/assets/curso/tema1/t1_44.png", alt="alt").m-auto

      .crd.crd--yellow(titulo="TMS")
        h4.px-4.pt-4 TMS (<i>Transport Management System</i>)
        p.mb-4.px-4  Permite planear y optimizar el transporte, seleccionar el proveedor, rastrear mercancas, optimizar cargas y manejar estadsticas.
        figure
          img(src="@/assets/curso/tema1/t1_45.png", alt="alt").m-auto

      .crd.crd--yellow(titulo="ERP")
        h4.px-4.pt-4 ERP (<i>Enterprise Resource Planning</i>)
        p.mb-4.px-4  Sistema Integral de Gestin Empresarial; la finalidad es automatizar la mayora de los procesos de la organizacin (administracin, logstica, produccin y comercializacin).
        figure
          img(src="@/assets/curso/tema1/t1_46.png", alt="alt").m-auto

      .crd.crd--yellow(titulo="EDI")
        h4.px-4.pt-4 EDI (<i>Electronic Data Interchange</i>)
        p.mb-4.px-4  Su objetivo es realizar el traspaso de documentos comerciales a un formato normalizado y pueda ser compartida la informacin entre empresas.  No es un correo electrnico, pues los datos deben contar con un estndar internacional.
        figure
          img(src="@/assets/curso/tema1/t1_47.png", alt="alt").m-auto

      .crd.crd--yellow(titulo="RFID")
        h4.px-4.pt-4 RFID (Sistema de navegacin mediante radiofrecuencia)
        p.mb-4.px-4  Aplicable al transporte y ubicaciones dentro y fuera del almacn.
        figure
          img(src="@/assets/curso/tema1/t1_48.png", alt="alt").m-auto

      .crd.crd--yellow(titulo="MPS")
        h4.px-4.pt-4 MPS (Plan Maestro de Produccin)
        p.mb-4.px-4 Programa de produccin a nivel superior que se usa para fijar el plan de produccin de cualquier fbrica decidiendo qu, cunto y en qu fecha se va a fabricar a medio plazo.
        figure
          img(src="@/assets/curso/tema1/t1_49.png", alt="alt").m-auto

      .crd.crd--yellow(titulo="<i>Business Intelligence</i>")
        h4.px-4.pt-4 <i>Business Intelligence</i> (Sistema inteligente)
        p.mb-4.px-4  Recopila, analiza y visualiza datos importantes sobre las actividades del almacn; a travs del cual se puede obtener informacin como: rutas de transporte, recogida, recepcin, tiempos de carga, emisiones de CO2, entre los principales.
        figure
          img(src="@/assets/curso/tema1/t1_50.png", alt="alt").m-auto

      .crd.crd--yellow(titulo="<i>Supply Chain Management</i> ")
        h4.px-4.pt-4 SCM (<i>Supply Chain Management</i>)
